import { useState } from "react"
import { CardGradientBackground } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { SelectedFile } from "../../../components/Hub/Files/files.types"
import { ChatPaneContainer } from "./components/ChatPane"
import { DocumentPane } from "./components/DocumentPane/DocumentPane"
import { FilesHeader } from "./components/FilesHeader"
import { FilesPane } from "./components/FilesPane"
import { useFilesPagePaneInfo } from "./hooks/useFilesPagePaneInfo"

function FilesPageV2() {
  const {
    chatPaneWidth,
    filesPaneWidth,
    documentPaneWidth,
    closeDocumentPane,
    closeChatPane,
    expandDocumentPane,
    openChatPane,
    unexpandDocumentPane,
  } = useFilesPagePaneInfo()

  const isFullView = !filesPaneWidth

  const [selectedFiles, setSelectedFiles] = useState<Array<SelectedFile>>([])

  return (
    <>
      {!isFullView && <CardGradientBackground />}

      <Box>
        <FilesHeader isFullView={isFullView} unexpandDocumentPane={unexpandDocumentPane} />

        <Box sx={{ display: "flex", position: "relative" }}>
          <FilesPane
            width={filesPaneWidth}
            openChatPane={openChatPane}
            isChatPaneOpen={!!chatPaneWidth}
            selectedFiles={selectedFiles}
            setSelectedFiles={setSelectedFiles}
          />

          <ChatPaneContainer
            width={chatPaneWidth}
            close={closeChatPane}
            selectedFiles={selectedFiles}
          />

          <DocumentPane
            isChatPaneOpen={!!chatPaneWidth}
            width={documentPaneWidth}
            close={closeDocumentPane}
            expandPane={expandDocumentPane}
            compressPane={unexpandDocumentPane}
            openChatPane={openChatPane}
          />
        </Box>
      </Box>
    </>
  )
}

export { FilesPageV2 }
